export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport {\n    DEMO_PATH_TOKEN,\n    NuiButtonModule,\n    NuiDocsModule,\n    NuiMenuModule,\n    NuiMessageModule,\n    NuiSearchModule,\n    NuiTextboxModule,\n    NuiToolbarModule,\n    SrlcStage,\n} from \"@nova-ui/bits\";\n\nimport {\n    ToolbarBasicExampleComponent,\n    ToolbarDocsExampleComponent,\n    ToolbarEmbeddedContentExampleComponent,\n    ToolbarItemTypesExampleComponent,\n    ToolbarSelectionExampleComponent,\n    ToolbarTestExampleComponent,\n    ToolbarVisualTestExampleComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: ToolbarDocsExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.ga,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"toolbar-test\",\n        component: ToolbarTestExampleComponent,\n    },\n    {\n        path: \"toolbar-visual-test\",\n        component: ToolbarVisualTestExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiButtonModule,\n        NuiToolbarModule,\n        NuiMenuModule,\n        NuiSearchModule,\n        NuiTextboxModule,\n        NuiMessageModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        ToolbarBasicExampleComponent,\n        ToolbarDocsExampleComponent,\n        ToolbarEmbeddedContentExampleComponent,\n        ToolbarSelectionExampleComponent,\n        ToolbarItemTypesExampleComponent,\n        ToolbarTestExampleComponent,\n        ToolbarVisualTestExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class ToolbarModule {\n}\n";